.oferta {
  width: 55%;
  padding-left: 16px;
  padding-top: 32px;
  padding-bottom: 16px;
}

.oferta_button {
  display: flex;
  text-align: start;
  font-weight: 500;
  cursor: pointer;
  padding-left: 16px;
}

.footer_law {
  display: flex;
  justify-content: space-between;
  gap: 16px;
  padding: 80px 16px 0px 16px;
  color: var(--navi-name-color);
}

.footer_law__CPS {
  width: 30%;
}

.oferta_button__arrow {
  position: relative;
  top: 6px;
}

@media (max-width: 768px) {
  .oferta_button__text {
    width: 206px;
  }

  .footer_law {
    flex-direction: column;
    text-align: center;
  }

  .footer_law__CPS {
    width: 100%;
  }

  .oferta_button {
    display: flex;
    justify-content: space-between;
    font-weight: 500;

    text-align: start;
  }
}

@media (min-width: 769px) {
  .oferta_button__text {
    width: 316px;
  }
}
